














































































































.right-banner {
    position: relative;

    .home-header-links {
        position: absolute;
        top: -35px;
        left: 20px;

        ul {
            display: flex;

            li {
                margin: 0 20px;

                > a {
                    font-size: 16px;
                    font-weight: 600;
                    color: #222222;

                    &:hover {
                        color: #f04d4e;
                    }

                    &.takeout:hover {
                        color: #fbc700;
                    }

                    &.apartment:hover {
                        color: #FDC411;
                    }

                    &.business:hover {
                        color: #31BBAC;
                    }
                }
            }
        }
    }

    .slide {
        margin: 10px;

        img {
            height: 240px;
        }
    }

    .entertainment {
        margin: 10px 10px 0 0;

        img {
            width: 100%;
            height: 240px;
        }
    }

    .user-info {
        box-sizing: border-box;
        background-color: #fff;
        margin-top: 10px;
        height: 240px;
        display: flex;
        flex-direction: column;
        align-items: center;
        padding: 30px;

        button {
            width: 118px;
            margin: 5px;
        }

        h4 {
            img {
                width: 47px;
                height: 47px;
                border-radius: 50%;
                border: 4px solid #E5E5E5;
            }
        }

        .user-name {
            font-size: 16px;
            margin-bottom: 10px;
        }
    }

    .r-hotel, .r-music, .r-coop {
        height: 165px;
    }

    .r-hotel {
        width: calc(100% - 20px);
        margin: 0 10px;
    }

    .r-music {
        width: calc(100% - 10px);
        margin-right: 10px;
    }

    .r-coop {
        width: 147px;
    }

    .app-download {
        height: 165px;
        width: 100%;
        background-color: #fff;
        display: flex;
        flex-direction: column;
        align-items: center;

        img {
            width: 95px;
            height: 95px;
            padding-top: 10px;
        }

        h3 {
            font-size: 16px;
            font-weight: 600;
        }

        p {
            font-weight: 500;
            margin-top: 2px;
            font-size: 12px;
            span {
                color: #f04d4e;
            }
        }

    }
}
